<template>
  <q-select
    dense
    outlined
    emit-value
    option-value="id"
    class="rounded text-body2 input-white text-primary"
    popup-content-class="bg-white rounded-bottom shadow-ui"
    input-class="text-body1 letter-space-normal text-primary"
    :options="listBrandTypes"
    v-model="selectModel"
  >
    <template v-slot:option="scope">
      <q-item
        clickable
        v-bind="scope.itemProps"
        :class="
          scope.selected
            ? 'bg-accent text-secondary'
            : 'text-primary hover-bg-accent'
        "
        class="hover-text-secondary"
        @click="scope.opt?.action"
      >
        <q-item-section avatar v-if="scope.opt?.icon" style="min-width: unset">
          <q-icon size="xs" :name="scope.opt?.icon" />
        </q-item-section>
        <q-item-section>
          <q-item-label>
            {{ scope.opt?.label || scope.opt }}
          </q-item-label>
        </q-item-section>
      </q-item>
    </template>

    <template v-slot:selected>
      <span :class="selectModel ? '' : 'text-placeholder'">{{
        selectModel || placeholder
        /* brandCategories[selectModel]?.label || placeholder */
      }}</span>
    </template>
  </q-select>
</template>

<script>
import { defineComponent } from 'vue';
import { mapState } from 'pinia';
import useGrowmodo from 'stores/growmodo';

export default defineComponent({
  name: 'SelectBrandType',

  props: {
    placeholder: {
      type: String,
      default: 'Select category',
    },
    initModel: {
      type: [String, Number],
      default: '',
    },
  },

  watch: {
    initModel: {
      async handler(val) {
        if (val) this.selectModel = val;
      },
    },
  },

  data() {
    return {
      selectModel: '',
    };
  },

  mounted() {
    if (this.initModel) this.selectModel = this.initModel;
  },

  computed: {
    ...mapState(useGrowmodo, ['brandCategories']),

    listBrandTypes() {
      return Object.values(this.brandCategories);
    },
  },
});
</script>
