                {{ _r.value }}
              </span>
            </q-td>
            <q-td
              :class="scope.selected ? 'bg-accent' : 'bg-transparent'"
              style="
                padding-right: 24px;
                padding-top: 18px;
                padding-bottom: 18px;
              "
            >
              <div class="q-gutter-sm text-right">
                <q-btn
                  flat
                  round
                  size="12px"
                  color="grey-7"
                  icon="icon-trash-01"
                  class="hover-text-negative hover-bg-accent"
                  :disable="itemsLoading"
                />
              </div>
            </q-td>
          </q-tr>
        </template>

        <!-- Bottom -->
        <template v-slot:bottom="scope">
          <div
            v-if="$q.screen.lt.xs || $q.screen.xs"
            class="q-py-md row full-width items-center justify-center q-col-gutter-sm q-virtual-scroll--skip"
          >
            <!-- Pagination -->
            <q-pagination
              unelevated
              boundary-numbers
              v-model="pagination.page"
              :disable="itemsLoading"
              color="text-grey-6"
              class="color-primary2 rounded"
              active-color="grey-2"
              active-text-color="primary"
              :max="scope.pagesNumber"
              :max-pages="6"
            />
          </div>
          <div
            class="q-py-md row full-width items-center justify-between q-col-gutter-sm q-virtual-scroll--skip"
          >
            <!-- Previous Page -->
            <div class="text-left" :class="$q.screen.gt.xs ? 'col-2' : 'col-6'">
              <q-btn
                dense
                outline
                no-caps
                :disable="scope.isFirstPage"
                @click="scope.prevPage"
                class="rounded q-px-md slide-hover"
                :class="
                  scope.isFirstPage
                    ? 'color-primary2'
                    : 'text-primary hover-text-secondary'
                "
              >
                <q-icon
                  size="xs"
                  name="icon-arrow-narrow-left"
                  :class="!scope.isFirstPage ? 'slide-hover-right-md' : ''"
                />
                <span class="q-pl-sm">Previous</span>
              </q-btn>
            </div>

            <!-- Pagination -->
            <q-pagination
              v-if="$q.screen.gt.xs"
              unelevated
              boundary-numbers
              v-model="pagination.page"
              :disable="itemsLoading"
              color="text-grey-6"
              class="color-primary2 rounded"
              active-color="grey-2"
              active-text-color="primary"
              :max="scope.pagesNumber"
              :max-pages="6"
            />

            <!-- Next Page -->
            <div
              class="text-right"
              :class="$q.screen.gt.xs ? 'col-2' : 'col-6'"
            >
              <q-btn
                dense
                outline
                no-caps
                :disable="scope.isLastPage"
                @click="scope.nextPage"
                class="rounded q-px-md slide-hover"
                :class="
                  scope.isLastPage
                    ? 'color-primary2'
                    : 'text-primary hover-text-secondary'
                "
              >
                <span class="q-pr-sm">Next</span>
                <q-icon
                  size="xs"
                  name="icon-arrow-narrow-right"
                  :class="!scope.isLastPage ? 'slide-hover-left-md' : ''"
                />
              </q-btn>
            </div>
          </div>
        </template>

        <!-- No Data -->
        <template v-slot:no-data="{ icon, message, filter }">
          <div
            class="q-py-md full-width row flex-center color-primary2 q-gutter-sm"
          >
            <q-icon
              size="sm"
              :color="itemsLoading ? 'secondary' : 'negative'"
              :name="
                itemsLoading
                  ? 'icon-download-cloud-01'
                  : filter
                  ? 'icon-filter-funnel-01'
                  : icon
              "
            />
            <span
              v-if="itemsLoading"
              class="text-body2 color-primary2 font-medium"
            >
              {{ message }}
            </span>
            <span v-else class="text-body2 color-primary2 font-medium">
              No
              {{ tableTab === 'tasks' ? 'Tasks' : 'Projects' }} found for this
              brand.
            </span>
          </div>
        </template>
      </q-table>
      <image-lightbox
        :visible="showLightBox"
        :imgs="lightboxImages"
        :index="lightboxIndex"
        @hide="showLightBox = false"
      />
    </q-card>
  </q-page>
</template>

<script>
import { defineComponent } from 'vue';
import CardIconBox from 'src/components/Helpers/CardIconBox.vue';
import { mapActions, mapState } from 'pinia';
import useUser from 'stores/user';
import useGrowmodo from 'stores/growmodo';
import useHelpers from 'stores/helpers';
import { date, debounce, format } from 'quasar';
import {
  avatarNameAbbr,
  checkIfObject,
  searchInObjectArray,
} from 'src/assets/scripts/functions';
import ColorPickerCard from 'src/components/Helpers/ColorPickerCard.vue';
import SelectBrandType from 'src/components/Helpers/SelectBrandType.vue';
import FileUploader from 'src/components/Helpers/FilesUploader.vue';
import UploadSlot from 'src/components/Helpers/Slots/UploadSlot.vue';
import { FileUploader_Mixin } from 'src/mixins';
const { capitalize } = format;

export default defineComponent({
  name: 'BrandOverview',

  mixins: [FileUploader_Mixin],

  components: {
    CardIconBox,
    ColorPickerCard,
    SelectBrandType,
    FileUploader,
    UploadSlot,
  },

  data() {
    return {
      title: 'Brand Overview',
      showLightBox: false,
      lightboxIndex: 0,
      lightboxImages: [],
      brandTab: 'general',
      searchQuery: '',
      itemsLoading: false,
      removedFile: null,
      listTableDense: true,
      selectedFiles: [],
      visibleColumns: [],
      onRequestDelayed: null,
      orgTmpItems: [],
      tableTab: 'tasks',
      pagination: {
        descending: true,
        page: 1,
        rowsPerPage: 10,
        rowsNumber: 0,
      },
      brand: { colors: {}, customColors: [], social: {} },
      avatarNameAbbr,
      capitalize,
      editing: false,
    };
  },

  async mounted() {
    this.$root.setMeta({ title: this.title });
    this.visibleColumns = this.columns
      .map((c) => c.name) // Get all the column names
      .filter((e) => !['id'].includes(e)); // Hide some columns initially

    this.onRequestDelayed = debounce(this.onRequest, 1000);
    this.onRequest({
      pagination: this.pagination,
      filter: this.filter,
    });
    this.updateLightBox();
  },

  computed: {
    ...mapState(useHelpers, ['socialMedia']),
    ...mapState(useUser, [
      'user',
      'selectedOrgBrands',
      'selectedOrgUsers',
      'selectedOrgProjects',
      'selectedOrgTasks',
    ]),
    ...mapState(useGrowmodo, [
      'brandCategories',
      'tasks',
      'taskDirectory',
      'projects',
      'projectDirectory',
    ]),

    brandGID() {
      return this.$route.params?.brandGID;
    },

    orgSelectedItems() {
      return this[this.tableTab]
        ? Object.values(
            this[
              this.tableTab === 'tasks'
                ? 'selectedOrgTasks'
                : 'selectedOrgProjects'
            ]?.[this.brandInfo?.id] || {}
          )
        : [];
    },

    brandInfo() {
      return this.selectedOrgBrands[this.brandGID] || {};
    },

    columns() {
      return this.tableTab === 'tasks'
        ? [
            {
              name: 'name',
              required: true,
              label: 'Task Name',
              align: 'left',
              field: (row) => row.name,
              format: (val) => val,
              sortable: true,
            },
            {
              name: 'id',
              label: 'ID',
              align: 'left',
              field: (row) => row.id,
              format: (val) => val,
              sortable: true,
              sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
            },
            {
              name: 'project',
              label: 'Project',
              align: 'left',
              field: (row) => row.project_gid,
              format: (val) =>
                this.selectedOrgProjects?.[this.brandInfo?.id]?.[val]?.name,
              sortable: true,
              sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
            },
            {
              name: 'task_priority',
              label: 'Task Priority',
              align: 'left',
              field: (row) => row.task_priority,
              format: (val) => val,
              sortable: true,
              sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
            },
            {
              name: 'task_type',
              label: 'Task Type',
              align: 'left',
              field: (row) => row.task_id,
              format: (val) =>
                this.taskDirectory[this.tasks[val]?.tags?.[0]]?.label,
              sortable: true,
              sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
            },
            {
              name: 'created_on',
              label: 'Created On',
              align: 'left',
              field: (row) => row.created_on,
              format: (val) => `${date.formatDate(val, 'MMM D, YYYY')}`,
              sortable: true,
              sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
            },
          ]
        : [
            {
              name: 'name',
              required: true,
              label: 'Project Name',
              align: 'left',
              field: (row) => row.name,
              format: (val) => val,
              sortable: true,
            },
            {
              name: 'id',
              label: 'ID',
              align: 'left',
              field: (row) => row.id,
              format: (val) => val,
              sortable: true,
              sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
            },
            {
              name: 'created_on',
              label: 'Created On',
              align: 'left',
              field: (row) => row.created_on,
              format: (val) => `${date.formatDate(val, 'MMM D, YYYY')}`,
              sortable: true,
              sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
            },
          ];
    },
  },

  watch: {
    searchQuery: {
      async handler(val) {
        this.onRequest(
          {
            pagination: this.pagination,
            filter: { search: val },
          },
          1000
        );
      },
    },
    brandTab: {
      async handler(val) {
        setTimeout(this.updateLightBox, 500);
      },
    },
    tableTab: {
      async handler(val) {
        this.onRequest(
          {
            pagination: this.pagination,
            filter: { search: this.searchQuery },
          },
          1000
        );
        setTimeout(this.updateLightBox, 500);
      },
    },
    orgSelectedItems: {
      async handler(val) {
        this.onRequest(
          {
            pagination: this.pagination,
            filter: { search: this.searchQuery },
          },
          1000
        );
      },
      deep: true,
    },
    pagination: {
      async handler(val) {
        this.onRequest(
          {
            pagination: val,
            filter: { search: this.searchQuery },
          },
          1000
        );
      },
      deep: true,
    },
  },

  methods: {
    ...mapActions(useHelpers, ['getSocialInfo', 'formatMediaPlatformIconSrc']),

    updateLightBox() {
      const lightboxElems = document.querySelectorAll('.with-lightbox');
      lightboxElems.forEach((e, i) => {
        let img = e.querySelector('img');
        this.lightboxImages.push({
          src: img.src,
          title: img.title,
          alt: img.alt,
        });
        e.onclick = () => {
          this.lightboxIndex = i;
          this.showLightBox = true;
        };
      });
    },

    filterMethod(dataArray, filter) {
      return filter && checkIfObject(filter) && Object.values(filter).join('')
        ? filter?.search
          ? searchInObjectArray(dataArray, filter?.search, ['name'])
          : dataArray
        : dataArray.slice();
    },

    // Search
    fetchFromServer(startRow, count, filter, sortBy, descending) {
      const data = this.filterMethod(this.orgSelectedItems, filter);

      // handle sortBy
      if (sortBy) {
        const sortFn =
          sortBy === 'desc'
            ? descending
              ? (a, b) => (a.name > b.name ? -1 : a.name < b.name ? 1 : 0)
              : (a, b) => (a.name > b.name ? 1 : a.name < b.name ? -1 : 0)
            : descending
            ? (a, b) => parseFloat(b[sortBy]) - parseFloat(a[sortBy])
            : (a, b) => parseFloat(a[sortBy]) - parseFloat(b[sortBy]);
        data.sort(sortFn);
      }

      return data.slice(startRow, startRow + count);
    },

    getRowsNumberCount(filter) {
      if (
        !filter ||
        !(checkIfObject(filter) && Object.values(filter).join(''))
      ) {
        return this.orgSelectedItems.length;
      }
      let count = 0;
      this.filterMethod(this.orgSelectedItems, filter).forEach((e) => {
        count++;
      });
      return count;
    },

    onRequest(props) {
      const { page, rowsPerPage, sortBy, descending } = props.pagination;
      const filter = props.filter;

      this.itemsLoading = true;

      // emulate server
      setTimeout(() => {
        // update rowsCount with appropriate value
        this.pagination.rowsNumber = this.getRowsNumberCount(filter);

        // get all rows if "All" (0) is selected
        const fetchCount =
          rowsPerPage === 0 ? this.pagination.rowsNumber : rowsPerPage;

        // calculate starting row of data
        const startRow = (page - 1) * rowsPerPage;

        // fetch data from "server"
        const returnedData = this.fetchFromServer(
          startRow,
          fetchCount,
          filter,
          sortBy,
          descending
        );

        // clear out existing data and add new
        this.orgTmpItems.splice(0, this.orgTmpItems.length, ...returnedData);

        // don't forget to update local pagination object
        this.pagination.page = page;
        this.pagination.rowsPerPage = rowsPerPage;
        this.pagination.sortBy = sortBy;
        this.pagination.descending = descending;

        // ...and turn of loading indicator
        this.itemsLoading = false;
      }, 750);
    },
  },
});
</script>
